@use 'variables' as v;
@use 'sass:color';

/* ===========================
   Tema Oscuro
   =========================== */
body.dark {
  background-color: v.$azul-oscuro;
  color: v.$blanco;

  //SweetAlert
  .minimal-alert {
    color: v.$azul;
    background-color: v.$azul-oscuro;
    border: .1rem solid v.$azul;
  }

//SweetAlert

  .shadowbox {
  -webkit-box-shadow: 0px 0px 10px 0px #3B82F6; 
  box-shadow: 0px 0px 10px 0px #3B82F6;
  }

  .header {
    background-color: v.$azul-oscuro;
    border-bottom: 1px solid v.$azul;
  }

  .logo, .spanTitulo {
    color: v.$blanco;
  }

  .actions {
    .create-btn, .search-btn {
      background-color: v.$gris-oscuro;
      border: .1rem solid v.$azul;
      color: v.$blanco;

      &:hover {
        background-color: v.$azul;
        color: v.$blanco;
      }
    }
  }

  .shadowbox {
    background-color: v.$gris-oscuro;
    color: v.$blanco;
  }

  .footer {
    background-color: v.$azul-oscuro;
    color: v.$gris-claro;
    border-top: .1rem solid v.$azul;

    &__texto {
      color: v.$gris-claro;
    }

    &__year {
      color: v.$gris-claro;
    }
  }

  .menu-btn {
    color: v.$blanco;

    &:hover {
      color: v.$azul;
    }
  }

  .dropdown-menu {
    background-color: v.$gris-oscuro;
    border: .1rem solid v.$azul;

    ul {
      li{
        a {
          color: v.$blanco;

          &:hover {
            background: v.$azul;
            color: v.$blanco;
          }
        }
      }
    }
  }

  .footer {
    .linkdln {
      a {
        color: v.$blanco;

        &:hover {
          color: v.$azul;
        }
      }
    }
  }

  //Inicio register
  .register {

    &__form {
      &--input {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
        border: .1rem solid v.$azul;
      }

    }

    &__options {
      &--google {
        background-color: v.$gris-oscuro;
        border: .1rem solid v.$azul;
        color: v.$azul;

        &:hover {
          background-color: v.$azul;
          color: v.$blanco;
        }
      }
    }
  }

  //Fin register

  //Inicio login

  .login {

    &__form {
      &--input {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
        border: .1rem solid v.$azul;
      }

    }
    &__options {
      &--google {
        background-color: v.$gris-oscuro;
        border: .1rem solid v.$azul;
        color: v.$azul;

        &:hover {
          background-color: v.$azul;
          color: v.$blanco;
        }
      }
    }
  }

  //Fin login

  //Olvidar contrase침a

  .forgot {
    &__form {
      &--input {
        background-color: v.$azul-oscuro;
        color: v.$blanco;
        border: .1rem solid v.$azul;
      }
    }
  }

  //Fin olvidar contrase침a

  //inicio confirmar contrase침a

  .reset {
    &__form {
      &--input {
        background-color: v.$azul-oscuro;
        color: v.$blanco;
        border: .1rem solid v.$azul;
      }
    }
  }  //fin confirmar contrase침a

  //Inicio del search.scss
  .search {
    &__titulo {
      color: v.$blanco;
    }

    &__input {
      background-color: v.$azul-oscuro;
      border: .2rem solid v.$azul;
      color: v.$blanco;

      &::placeholder {
        color: v.$negro;
      }
    }

    //Inicio de filtrosearch

    &__filter--btn {
      background-color: v.$azul-oscuro;
      border: .1rem solid v.$azul;
      color: v.$blanco;

      &:hover {
        background-color: v.$azul;
        color: v.$blanco;
      }
    }
    &__filter--panel {
      background-color: color.adjust(v.$azul-oscuro, $lightness: 10%);
      border: .1rem solid v.$azul;
    }

    &__filter--option {
      background: v.$gris-oscuro;
      color: v.$blanco;
      border: .1rem solid v.$azul;

      &:hover {
        background-color: v.$azul;
        color: v.$blanco;
      }
    }

    //fin de filtrosearch

    //INICIO LISTADO DEL SEARCH.SCSS
    &__results {

      .note-item {
        background: v.$gris-oscuro;
        border-color: color.adjust(v.$azul, $lightness: -10%);
        box-shadow: none;

        &:hover {
          -webkit-box-shadow: 2px 4px 17px 3px v.$negro; 
          box-shadow: 2px 4px 17px 3px v.$negro;
        }

        .info {
          h3 {
            color: v.$blanco;
          }
          p {
            color: color.adjust(v.$blanco, $lightness: -40%);
          }
        }

        .actions {

          .edit-btn {
            background: v.$azul;
          }

          .delete-btn {
            background: #ff6b6b;
          }
        }
      }
    }
    //FIN LISTADO DEL SEARCH.SCSS


    //Inicio pagination
    &__pagination {
      &--page-btn {
        background-color: v.$azul-oscuro;
        border: .1rem solid v.$azul;
        color: v.$blanco;

        &.active {
          background: #3085d6;
        color: white;
        border-color: #3085d6;
        }
            /* 游댮 AGREGA ESTO */
        &:disabled {
          opacity: 0.35;
          cursor: not-allowed;
          pointer-events: none;
        }
      }
    }
  }
  //Fin del search.scss




  //INICIO _CREAR.SCSS
  .crear {
    &__titulo {
      color: v.$blanco;
    }

    &__inputTitulo, &__inputContenido {
      background-color: v.$gris-oscuro;
      color: v.$blanco;
      border: .1rem solid v.$azul;
    }
  }

  //FIN _CREAR.SCSS

  //INICIO EDITAR.SCSS
.editar {
  color: v.$blanco;

  .editar__titulo {
    color: v.$blanco;
  }

  .editar__input,
  .editar__textarea {
    background: transparent;
    border-color: color.adjust(v.$gris-oscuro, $lightness: 10%);
    color: v.$blanco;

    &:focus {
      border-color: v.$azul;
    }
  }

  .editar__delete-btn {
    background: #ff6b6b;
  }
}


  //FIN EDITAR.SCSS


  //Inicio boton  de ajuste
  .lang-switcher, .theme-switcher {
  body.dark & button {
    border-color: v.$azul;
    color: v.$blanco;

    &:hover {
      background-color: v.$azul;
      color: v.$blanco;
    }
  }
}

.minimal-alert {
  border-radius: 14px !important;
  padding: 12px 18px !important;
  backdrop-filter: blur(8px);
  border: 1px solid rgba(0,0,0,0.05);
  font-family: 'Inter', sans-serif;
  font-size: 1.5rem;
  box-shadow: 
    0 2px 8px rgba(0,0,0,0.08),
    0 6px 24px rgba(0,0,0,0.06) !important;
}

.minimal-alert {
  border: 1px solid rgba(255,255,255,0.08);
}
  //fin boton de ajuste
}




